


































import PopupMixin from '@/mixins/popup'
import { assign, includes, trim } from 'lodash'
import { Component, Mixins, Prop, Watch } from 'vue-property-decorator'
import { namespace } from 'vuex-class'
import FiltersPopup from './FilterPopupMainReport.vue'

const beReport = namespace('beReport')

@Component({ components: { FiltersPopup } })
export default class ReportFilter extends Mixins(PopupMixin) {
  @Prop({ required: false, type: String, default: 'pl9a' }) type!: 'pl9a' | 'pl9b' | 'pl9c' | 'pl9d' | 'pl9e' | 'pl9f' | 'pl9g' | 'pl9h' | 'pl9i' | 'pl9year'
  @Prop({ required: true, type: Object }) filters!: Record<string, any>
  @Prop({ required: false, type: String, default: 'Tìm kiếm...' }) searchPlaceholder!: string

  @beReport.Getter('getFilter') getFilter!: (rerport: 'pl9a' | 'pl9b' | 'pl9c' | 'pl9d' | 'pl9e' | 'pl9f' | 'pl9g' | 'pl9h' | 'pl9i' | 'pl9year') => {
    report: string
    filters: Record<string, any>
  }
  @beReport.Action('setReportFilter') setReportFilter!: (data: { report: string; filters: Record<string, any> }) => void

  search = ''
  groupFilters: Record<string, any> = {
    month: new Date().getMonth() + 1,
    year: new Date()
  }

  sortType = '' // BRANCH_ID_INCREASE, REVENUE_INCREASE, COMMISSION_INCREASE, BRANCH_ID_DECREASE, REVENUE_DECREASE, COMMISSION_DECREASE

  get sorts(): Array<Record<string, any>> {
    if (includes(['pl9a', 'pl9b', 'pl9c'], this.type)) {
      return [
        {
          command: 'OFFICE_ID_INCREASE',
          label: 'Mã đơn vị kinh doanh',
          divided: false
        },
        {
          command: 'REVENUE_INCREASE',
          label: 'Doanh số lũy kế',
          divided: true
        },
        {
          command: 'COMMISSION_INCREASE',
          label: 'Hoa hồng lũy kế',
          divided: false
        },
        {
          command: 'RESULT_YEAR_REVENUE_PERCENT_INCREASE',
          label: 'Tỷ lệ hoàn thành doanh số năm',
          divided: true
        },
        {
          command: 'RESULT_MONTH_REVENUE_PERCENT_INCREASE',
          label: 'Tỷ lệ hoàn thành doanh số tháng',
          divided: false
        },
        {
          command: 'RESULT_YEAR_COMMISSION_PERCENT_INCREASE',
          label: 'Tỷ lệ hoàn thành thu phí năm',
          divided: false
        },
        {
          command: 'RESULT_MONTH_COMMISSION_PERCENT_INCREASE',
          label: 'Tỷ lệ hoàn thành thu phí tháng',
          divided: false
        }
      ]
    } else if (this.type === 'pl9d') {
      return [
        {
          command: 'OFFICE_ID_INCREASE',
          label: 'Mã đơn vị kinh doanh',
          divided: false
        },
        {
          command: 'REVENUE_INCREASE',
          label: 'Doanh số năm 1 lũy kế',
          divided: true
        }
      ]
    } else if (this.type === 'pl9e') {
      return [
        {
          command: 'OFFICE_ID_INCREASE',
          label: 'Mã đơn vị kinh doanh',
          divided: false
        },
        {
          command: 'REVENUE_INCREASE',
          label: 'Doanh số K2 lũy kế',
          divided: true
        }
      ]
    } else if (this.type === 'pl9f') {
      return [
        {
          command: 'OFFICE_ID_INCREASE',
          label: 'Mã đơn vị kinh doanh',
          divided: false
        },
        {
          command: 'COMMISSION_INCREASE',
          label: 'Hoa hồng năm 1 lũy kế',
          divided: true
        }
      ]
    } else if (this.type === 'pl9g') {
      return [
        {
          command: 'OFFICE_ID_INCREASE',
          label: 'Mã đơn vị kinh doanh',
          divided: false
        },
        {
          command: 'COMMISSION_INCREASE',
          label: 'Hoa hồng K2 lũy kế',
          divided: true
        }
      ]
    } else if (this.type === 'pl9h') {
      return [
        {
          command: 'OFFICE_ID_INCREASE',
          label: 'Mã đơn vị kinh doanh',
          divided: false
        },
        {
          command: 'REVENUE_INCREASE',
          label: 'Chương trình thi đua lũy kế',
          divided: true
        }
      ]
    } else if (this.type === 'pl9i') {
      return [
        {
          command: 'TRANSACTION_OFFICE_CODE',
          label: 'Mã đơn vị kinh doanh',
          divided: false
        },
        {
          command: 'REVENUE_CK_TOTAL_AMOUNT',
          label: 'Tổng doanh số',
          divided: true
        },
        {
          command: 'CK_NUM_OF_AMOUNT',
          label: 'Số hợp đồng',
          divided: true
        }
      ]
    } else if (this.type === 'pl9year') {
      return [
        {
          command: 'TRANSACTION_OFFICE_CODE',
          label: 'Mã đơn vị kinh doanh',
          divided: false
        },
        {
          command: 'REVENUE_CK_TOTAL_AMOUNT',
          label: 'Ngày tháng',
          divided: true
        }
      ]
    }

    return []
  }

  @Watch('search', { immediate: false }) watchSearch(new_: string): void {
    this.searchText(new_)
  }

  searchText(value: string): void {
    this.groupFilters.search = trim(value)
    this.$emit(
      'change',
      assign(this.groupFilters, {
        search: trim(value),
        sortType: this.sortType
      })
    )
    this.setReportFilter({
      report: this.type,
      filters: assign(this.groupFilters, {
        search: trim(value),
        sortType: this.sortType
      })
    })
  }

  handleClick(): void {
    this.setOpenPopup({
      popupName: 'reportFilterPopup',
      isOpen: true
    })
  }

  handeChangeFilters(filters: Record<string, any>): void {
    this.groupFilters = {
      ...this.groupFilters,
      ...filters
    }

    this.setReportFilter({
      report: this.type,
      filters: assign(this.groupFilters, {
        search: this.search,
        sortType: this.sortType
      })
    })
    this.$emit(
      'change',
      assign(this.groupFilters, {
        search: this.search,
        sortType: this.sortType
      })
    )
  }

  handleCommand(command: string): void {
    if (this.sortType === command) {
      this.sortType = ''
    } else {
      this.sortType = command
    }

    this.setReportFilter({
      report: this.type,
      filters: assign(this.groupFilters, {
        search: this.search,
        sortType: this.sortType
      })
    })
    this.$emit(
      'change',
      assign(this.groupFilters, {
        search: this.search,
        sortType: this.sortType
      })
    )
  }

  init(): void {
    const filters = this.getFilter(this.type)
    if (filters) {
      this.groupFilters = assign(this.groupFilters, filters.filters)
      delete this.groupFilters.search
      delete this.groupFilters.sortType
      this.search = filters.filters.search
      this.sortType = filters.filters.sortType
    }
    this.$emit(
      'change',
      assign(this.groupFilters, {
        search: this.search,
        sortType: this.sortType
      })
    )
  }

  created(): void {
    this.init()
  }
}
