import { assign } from 'lodash'
import getRepository from '@/services'
import JasperRepository from '@/services/repositories/jasper'
import { Vue, Component } from 'vue-property-decorator'

@Component
export default class ExportMixin extends Vue {
  reportRes2: JasperRepository | undefined = undefined

  isExcelLoading = false
  isPdfLoading = false

  async handleExport(
    type: 'pl9a' | 'pl9b' | 'pl9c' | 'pl9d' | 'pl9e' | 'pl9f' | 'pl9g' | 'pl9h',
    format: 'excel' | 'pdf' = 'excel',
    filters?: Record<string, any>,
    table?: Record<string, any>,
    data?: Array<Record<string, any>>
  ): Promise<void> {
    if (format === 'excel') this.isExcelLoading = true
    if (format === 'pdf') this.isPdfLoading = true
    let rpType = ''
    switch (type) {
      case 'pl9a':
        rpType = '9a'
        break
      case 'pl9b':
        rpType = '9b'
        break
      case 'pl9c':
        rpType = '9c'
        break
      case 'pl9d':
        rpType = '9d'
        break
      case 'pl9e':
        rpType = '9e'
        break
      case 'pl9f':
        rpType = '9f'
        break
      case 'pl9g':
        rpType = '9g'
        break
      default:
        rpType = '9h'
        break
    }
    if (data?.length) {
      try {
        const param = assign(
          {
            format: format
          },
          filters,
          table
        )
        delete param.locationSelect
        const response = await this.reportRes2?.exportReport(type, param, rpType)
        const url = window.URL.createObjectURL(new Blob([response.data]))
        const link = document.createElement('a')
        link.href = url
        const date = new Date()
        const time = `${date.getFullYear()}${date.getMonth() + 1}${date.getDate()}${date.getHours()}${date.getMinutes()}${date.getMilliseconds()}`
        link.setAttribute('download', `${type.toUpperCase()}_${time}.${format === 'pdf' ? 'pdf' : 'xlsx'}`)
        document.body.appendChild(link)
        link.click()
        this.isExcelLoading = false
        this.isPdfLoading = false
      } catch (error) {
        this.isExcelLoading = false
        this.isPdfLoading = false
      }
    } else {
      this.$message.error('Không tìm thấy dữ liệu xuất file')
      this.isExcelLoading = false
      this.isPdfLoading = false
    }
  }

  created(): void {
    this.reportRes2 = getRepository('jasper', {
      districtId: this.districtId
    })
  }
}
