











































































































































































































































































import PopupMixin from '@/mixins/popup'
import getRepository from '@/services'
import { LocationEntiry } from '@/services/entities/location'
import LocationRepository from '@/services/repositories/location'
import { assign, debounce, includes, size, trim } from 'lodash'
import { Component, Mixins, Prop } from 'vue-property-decorator'
import { namespace } from 'vuex-class'

const beReport = namespace('beReport')

@Component({ components: {} })
export default class FiltersPopup extends Mixins(PopupMixin) {
  @Prop({ required: true, type: String, default: 'pl9a' }) type!: 'pl9a' | 'pl9b' | 'pl9c' | 'pl9d' | 'pl9e' | 'pl9f' | 'pl9g' | 'pl9h' | 'pl9i'

  @beReport.Getter('getFilter') getFilter!: (rerport: 'pl9a' | 'pl9b' | 'pl9c' | 'pl9d' | 'pl9e' | 'pl9f' | 'pl9g' | 'pl9h' | 'pl9i') => {
    report: string
    filters: Record<string, any>
    location: Record<string, LocationEntiry[]>
  }

  isLoading = false

  filters: Record<string, any> = {
    month: new Date().getMonth() + 1,
    year: new Date(),
    areaIds: [],
    branchIds: [],
    officeIds: [],
    fromValue: '',
    toValue: '',
    selectRevenueLoanPercent: '',
    selectRevenueCapitalPercent: '',
    selectRevenueVisitorPercent: '',
    selectRevenueFamilyPercent: '',
    selectRevenueEmployeePercent: '',
    fromRevenue: '',
    toRevenue: '',
    selectMonthRevenue: '',
    selectYearRevenue: '',
    selectYearCommission: '',
    selectMonthCommission: '',
    toCommission: '',
    fromCommission: '',
    locationSelect: {
      areaIds: [],
      officeIds: [],
      branchIds: []
    }
  }

  locationRes: LocationRepository | undefined = undefined

  options: Array<any> = []

  dataLocation: Record<string, LocationEntiry[]> = {
    areaIds: [],
    officeIds: [],
    branchIds: []
  }

  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  // validateFilterForm = (rule?: any, value?: any, callback?: any) => {
  //   if (rule.field === 'numberRule' && Number(this.filters.fromRevenue) > Number(this.filters.toRevenue)) {
  //     callback(new Error(`Giá trị "Từ" phải nhỏ hơn "Đến"`))
  //   }
  //   if (rule.field === 'commission' && Number(this.filters.fromCommission) > Number(this.filters.toCommission)) {
  //     callback(new Error(`Giá trị "Từ" phải nhỏ hơn "Đến"`))
  //   }
  //   if (rule.field === 'revenue' && Number(this.filters.fromValue) > Number(this.filters.toValue)) {
  //     callback(new Error(`Giá trị "Từ" phải nhỏ hơn "Đến"`))
  //   }

  //   callback()
  // }

  // filterRules = {
  //   numberRule: [{ validator: this.validateFilterForm, trigger: 'blur' }],
  //   commission: [{ validator: this.validateFilterForm, trigger: 'blur' }],
  //   revenue: [{ validator: this.validateFilterForm, trigger: 'blur' }]
  // }

  query = {
    areas: {
      i: 0,
      ps: 20,
      search: ''
    },
    branches: {
      i: 0,
      ps: 20,
      search: ''
    },
    offices: {
      i: 0,
      ps: 20,
      search: ''
    }
  }
  listError: Record<string, boolean> = {
    commission: false,
    numberRule: false,
    value: false
  }
  showAction = true
  get isReportAbc(): boolean {
    return includes(['pl9a', 'pl9b', 'pl9c'], this.type)
  }
  get isPl9i(): boolean {
    return includes(['pl9i'], this.type)
  }
  handleChange(type: string, ids: number[]): void {
    console.log(type, ids)
    if (ids.length == 0 || ids.length < this.filters.locationSelect[type].length) {
      if (ids.length == 0) {
        this.filters.locationSelect[type] = []
      }
      if (ids.length < this.filters.locationSelect[type].length) {
        this.filters.locationSelect[type].length.pop()
      }
    } else {
      const lastId = ids[ids.length - 1]
      const itemSelect = this.dataLocation[type].find((item: LocationEntiry) => item.id == lastId)
      const check = this.filters.locationSelect[type].some((item: LocationEntiry) => item.id == itemSelect?.id)
      if (!check) {
        this.filters.locationSelect[type].push(itemSelect)
      }
    }
  }
  convertNumberToDecimal(value: string): void {
    this.filters[value] =
      //@ts-ignore
      this.$options.filters.formatNumber(this.convertDecimalToNumber(this.filters[value])) === '0'
        ? null
        : //@ts-ignore
          this.$options.filters.formatNumber(this.convertDecimalToNumber(this.filters[value]))
  }

  convertDecimalToNumber(number: string | number): number {
    return typeof number !== 'number' ? Number(number.replaceAll(',', '')) : number
  }

  handleChangeInput(type: string, value: string): void {
    this.convertNumberToDecimal(value)
    if (type === 'numberRule') {
      if (this.filters.toRevenue && this.filters.fromRevenue && this.convertDecimalToNumber(this.filters.toRevenue) < this.convertDecimalToNumber(this.filters.fromRevenue)) {
        this.listError.numberRule = true
      } else {
        this.listError.numberRule = false
      }
    }
    if (type === 'commission') {
      if (
        this.filters.toCommission &&
        this.filters.fromCommission &&
        this.convertDecimalToNumber(this.filters.toCommission) < this.convertDecimalToNumber(this.filters.fromCommission)
      ) {
        this.listError.commission = true
      } else {
        this.listError.commission = false
      }
    }
    if (type === 'revenue') {
      if (this.filters.fromValue && this.filters.toValue && this.convertDecimalToNumber(this.filters.toValue) < this.convertDecimalToNumber(this.filters.fromValue)) {
        this.listError.value = true
      } else {
        this.listError.value = false
      }
    }
  }

  remoteMethodGetArea(query: string): void {
    this.isLoading = true
    this.query.areas.search = trim(query)
    this.query.areas.i = 0
    this.query.areas.ps = 20
    const a = debounce(this.searchArea, 500)
    a(query)
  }

  remoteMethodGetBranch(query: string): void {
    this.isLoading = true
    this.query.branches.search = trim(query)
    this.query.branches.i = 0
    this.query.branches.ps = 20
    const a = debounce(this.searchBranch, 500)
    a(query)
  }

  remoteMethodGetOffice(query: string): void {
    this.isLoading = true
    this.query.offices.search = trim(query)
    this.query.offices.i = 0
    this.query.offices.ps = 20
    const a = debounce(this.searchOffice, 500)
    a(query)
  }

  loadAreas(): void {
    console.log(1)
    if (size(this.dataLocation.areaIds) === this.query.areas.i + this.query.areas.ps) {
      this.query.areas.i = size(this.dataLocation.areaIds)
      const a = debounce(this.searchArea, 500)
      a(this.query.areas.search, true)
    }
  }

  loadBranches(): void {
    console.log(1)
    if (size(this.dataLocation.branchIds) === this.query.branches.i + this.query.branches.ps) {
      this.query.branches.i = size(this.dataLocation.branchIds)
      const a = debounce(this.searchBranch, 500)
      a(this.query.branches.search, true)
    }
  }
  loadOffices(): void {
    console.log(1)
    if (size(this.dataLocation.officeIds) === this.query.offices.i + this.query.offices.ps) {
      this.query.offices.i = size(this.dataLocation.officeIds)
      const a = debounce(this.searchOffice, 500)
      a(this.query.offices.search, true)
    }
  }

  async searchArea(search: string, isAdd = false): Promise<any> {
    // if (trim(search) === '') {
    //   this.isLoading = false
    //   this.areas = []
    //   return
    // }
    try {
      const result = await this.locationRes?.list('area', {
        search: trim(search),
        i: this.query.areas.i,
        ps: this.query.areas.ps
      })
      if (isAdd) {
        const a = result || []
        this.dataLocation.areaIds = [...this.dataLocation.areaIds, ...a]
      } else {
        this.dataLocation.areaIds = result || []
      }

      this.isLoading = false
    } catch (error) {
      this.isLoading = false
      this.dataLocation.areas = []
    }
  }

  async searchBranch(search: string, isAdd = false): Promise<any> {
    // if (trim(search) === '') {
    //   this.isLoading = false
    //   this.branches = []
    //   return
    // }
    try {
      const result = await this.locationRes?.list('branch', {
        search: trim(search),
        i: this.query.branches.i,
        ps: this.query.branches.ps
      })

      if (isAdd) {
        const a = result || []
        this.dataLocation.branchIds = [...this.dataLocation.branchIds, ...a]
      } else {
        this.dataLocation.branchIds = result || []
      }
      this.isLoading = false
    } catch (error) {
      this.isLoading = false
      this.dataLocation.branchIds = []
    }
  }

  async searchOffice(search: string, isAdd = false): Promise<any> {
    // if (trim(search) === '') {
    //   this.isLoading = false
    //   this.offices = []
    //   return
    // }
    try {
      const result = await this.locationRes?.list('office', {
        search: trim(search),
        i: this.query.offices.i,
        ps: this.query.offices.ps
      })

      if (isAdd) {
        const a = result || []
        this.dataLocation.officeIds = [...this.dataLocation.officeIds, ...a]
      } else {
        this.dataLocation.officeIds = result || []
      }
      this.isLoading = false
    } catch (error) {
      this.isLoading = false
      this.dataLocation.officeIds = []
    }
  }

  resetData(): void {
    if (this.isReportAbc) {
      this.filters = {
        month: new Date().getMonth() + 1,
        year: new Date(),
        selectYearCommission: null,
        selectMonthCommission: null,
        toCommission: null,
        fromCommission: null,
        selectYearRevenue: null,
        selectMonthRevenue: null,
        toRevenue: null,
        fromRevenue: null,
        officeIds: [],
        branchIds: [],
        areaIds: []
      }
    } else {
      this.filters = {
        month: new Date().getMonth() + 1,
        year: new Date(),
        officeIds: [],
        branchIds: [],
        areaIds: [],
        toValue: null,
        fromValue: null,
        selectRevenueLoanPercent: null,
        selectRevenueCapitalPercent: null,
        selectRevenueVisitorPercent: null,
        selectRevenueFamilyPercent: null
      }
    }
    this.listError = {
      commission: false,
      numberRule: false,
      value: false
    }
  }

  handleSubmit(type: 'reset' | 'submit'): void {
    if (type === 'reset') {
      this.resetData()
      return
    }
    const checkValid = Object.keys(this.listError).every(key => !this.listError[key])
    if (checkValid) {
      this.$emit('change', this.filters)
      this.setOpenPopup({
        popupName: 'reportFilterPopup',
        isOpen: false
      })
      console.log('data', this.filters)
    }
  }

  reset(): void {
    this.dataLocation.areaIds = []
    this.dataLocation.branchIds = []
    this.dataLocation.officeIds = []
  }

  handleOpen(): void {
    if (this.$route.name === 'pl9bReport') {
      this.showAction = false
    }

    this.searchArea('')
    this.searchBranch('')
    this.searchOffice('')
  }

  handleClose(): void {
    this.reset()
  }

  created(): void {
    this.locationRes = getRepository('location', {
      districtId: this.districtId
    })

    const filters = this.getFilter(this.type)
    if (filters) {
      this.filters = assign(this.filters, filters.filters)
      this.dataLocation = { ...filters.filters.locationSelect }

      delete this.filters.search
      delete this.filters.sortType
    }
  }
}
